.qarr {
    // Panels
    .panel {

        .panel-wrapper {
            position: relative;
            background: white;
            border-radius: 5px;
            padding: 30px;
            box-sizing: border-box;
            border: 1px solid $c-border;

            .status-badge {
                position: absolute;
                top: 20px;
                right: 20px;
            }
        }

        &.panel-correspondence, &.panel-fields {
            margin-top: 50px;
        }

        &.panel-blue {
            .panel-header {
                background: $c-blue;
            }
        }

        .panel-header {
            position: relative;
            display: flex;
            margin: -30px -30px 0 -30px;
            padding: 25px;
            color: white;
            background: lighten($c-gray, 50);
            border-top-left-radius: 5px;
            border-top-right-radius: 5px;

            h2 {
                font-family: $font-bold;
                font-size: 16px;
                text-transform: uppercase;
                letter-spacing: .03em;
                margin: 0;
                @include antialias;
            }

            .header-icon {
                position: absolute;
                right: 25px;
                margin-top: -3px;
                font-size: 30px;
            }
        }

        .panel-body {
            padding-top: 25px;

            p { margin: 0; }

            hr {
                margin: 20px -30px;
            }
        }

        .panel-item {
            margin-bottom: 15px;
            @include antialias;

            &:last-child {
                margin-bottom: 0;
            }

            .panel-meta {
                font-size: 10px;
                color: $c-lightGray;
            }
        }

        .large-text {
            font-family: $font-regular;
            font-size: 16px;
            margin: 0;

            span {
                line-height: 1.6;
            }
        }

        &.has-response {
            .panel-response {
                margin-top: 50px;
                padding: 35px 25px 20px 25px;
                border: 1px dashed darken($c-border, 5);
                border-radius: 5px;
                background: #d8e3ec1f;
            }
        }

        .panel-actions {
            position: relative;
            display: flex;
            margin-top: 25px;
        }

        .pull-right {
            margin-left: auto;
        }

        .remove-entry {
            padding-left: 10px;
            margin-left: 10px;
            border-left: 1px solid $c-border;

            .icon {
                position: static;
                widows: 100%;
            }

            a {
                height: 40px;
                padding: 10px 3px;
            }
        }

        .panel-response {
            position: relative;

            .spinner {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }

            .response-container {
                &.faded {
                    opacity: .1;
                }
            }

            .response-author {
                display: flex;
                position: absolute;
                top: 5px;
                left: 10px;
                width: 100%;

                font-size: 10px;
                font-weight: 600;
                text-transform: uppercase;
                color: $c-lightGray;
                @include antialias;

                span {
                    color: darken($c-lightGray, 10);
                }

                .last {
                    margin-left: auto;
                    margin-right: 20px;
                }
            }

            .response-text {
                font-weight: 400;
                color: darken($c-lightGray, 20);
            }

            .response-actions {
                display: flex;
                position: relative;
                margin-top: 15px;

                & > a {
                    &:last-child {
                        margin-left: auto;
                    }
                }

                svg {
                    margin-right: 3px;
                }

                a:hover {
                    color: $c-blue;
                }

                #reply-to-feedback-btn {
                    display: inline;
                }
            }
        }

        .panel-progress {
            position: absolute;
            width: 300px;
            top: -22px;
            right: 0;

            .progressbar {
                top: 4px;
                left: auto;
                right: 0;
                width: 253px;
            }

            .progressbar-status {
                font-family: $font-roboto;
                font-weight: 600;
                letter-spacing: .05em;
            }
        }

        &.panel-answers {
            margin-top: 30px;

            .large-text {
                padding: 5px 0;
            }

            .panel-item {
                padding-bottom: 15px;
                border-bottom: 1px solid $c-border;

                &:last-child {
                    border: none;
                    padding: 0;
                }
            }

            .panel-item-status {
                font-family: $font-bold;
                font-size: 10px;
                letter-spacing: .05em;
                text-transform: uppercase;
                min-height: 20px;
                @include antialias;

                &.status-pending { color: $c-blue; }
                &.status-approved { color: $c-green; }
                &.status-rejected { color: $c-red; }

            }

            .panel-actions {
                display: flex;
                margin-top: 5px;

                .qarr-pipe {
                    color: lighten($c-gray, 30);
                    font-size: 10px;
                }

                a {
                    display: block;
                    font-family: $font-semiBold;
                    font-size: 10px;
                    color: $c-gray;
                    padding: 0 4px;
                    transition: all .3s ease;

                    &:first-child {
                        padding-left: 0;
                    }

                    &:hover {
                        color: lighten($c-gray, 30);
                    }

                    &.btn-approve {
                        &:hover {
                            color: $c-green;
                        }
                    }

                    &.btn-reject, &.btn-delete {
                        &:hover {
                            color: $c-red;
                        }
                    }

                    &.btn-delete {
                        margin-left: auto;
                    }
                }
            }
        }
    }

    // Flagged Panels
    .feedback-panel {
        .matched-word {
            font-size: 14px;
            padding: 1px 4px;
            background: $c-border;
            border-radius: 3px;
        }
    }
}


