.qarr {

    @include bp('max-width', $wide) {
        #crumbs {
            position: relative;
            padding: 0 !important;

            #nav-toggle {
                position: absolute;
                z-index: 100;
                left: 9px;
            }
        }
    }

    &.qarr-edit {
        #main-container #main #main-content #content-container #content {
            background: white;
        }

    }

    #main-container #main #main-content #content-container #content {
        background: $c-border;
    }


    #main-container #main #header {
        height: 98px;
        background: white;
        padding: 24px !important;
        box-shadow: 1px 2px 10px -2px rgba(0, 0, 0, 0.1);
        justify-content: flex-start;
    }

    .page-title-wrapper {
        display: flex;
        align-items: center;

        .page-title {
            h1 {
                line-height: normal !important;
            }

            p {
                margin: 0;
                line-height: 1;
                font-family: $font-italic;
                color: $c-lightGray;
            }
        }
    }

    .entry-title-wrapper {
        display: flex;
        align-items: center;
        padding: 10px 0 30px 0;

        .entry-title {
            display: flex;
            align-items: center;
        }

        .title-icon {
            margin-right: 8px;
            font-size: 20px;
        }

        h1 {
            margin: 0 10px 0 0;
            line-height: normal;
        }

        .lightswitch {
            margin-top: 5px;
        }
    }

    #qarr-crumbs {
        display: flex;
        align-items: center;
        color: $c-lightGray;
        margin-top: 2px;

        svg {
            position: relative;
            top: 1px;
            font-size: 7px;
            padding: 0 5px;
        }

        a {
            color: $c-lightGray;
            font-size: 11px;
            line-height: normal;

            &:hover {
                color: $c-purple;
            }
        }
    }

    .qarr-logo {
        display: block;
        @include size(45px, 29px);
        margin-right: 20px;
    }

    .list {
        .list-item {
            overflow: hidden;
            position: relative;
            display: flex;
            flex-direction: column;
            transition: all .3s ease;
            border-top: 1px solid $c-border;
            min-height: 43px;

            &:last-child {
                .item-wrapper {
                    border-bottom-left-radius: 5px;
                    border-bottom-right-radius: 5px;
                }
            }

            &.dragging {
                .item-wrapper {
                    border-right: 1px solid $c-border;
                    border-left: 1px solid $c-border;
                }
            }
            
            &.approve-item {
                .action-other {
                    display: none;
                }
            }

            &.reject-item {
                .action-approve {
                    opacity: 0;
                }
            }

            &.delete-item {
                .action-approve, .action-other {
                    opacity: 0;
                }
            }

            .item-actions {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                background-color: rgba($c-border, .4);

                .action-other {
                    margin-left: auto;
                }

                & > div {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: opacity .3s ease;
                }

                svg {
                    transition: all .3s ease;
                }
                
                a {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 26px;
                    color: $c-lightGray;

                    @include size(100px, 100%);


                    &.approved {
                        color: $c-green;
                        animation: scaleGreen .3s ease-in-out both;
                    }

                    &.rejected {
                        color: $c-red;
                        animation: scaleRed .3s ease-in-out both;
                    }
                }

                .delete-btn:hover {
                    color: $c-red;
                }

                .reject-btn:hover {
                    color: $c-red;
                }
            }

            .item-wrapper {
                position: relative;
                z-index: 1;
                background-color: white;
                padding: 10px 25px;
            }

            &.last {
                padding-bottom: 10px;
            }

            &.has-hover {
                &:hover {
                    box-shadow: 1px 2px 10px rgba(0, 0, 0, .1);
                    transition: all .3s ease-in-out;
                    z-index: 1;
                }
            }

            &.element-actions {
                display: flex;
                padding: 0;

                button, a {
                    align-self: center;
                    width: 100%;
                    display: inline-block;
                    border-radius: 0;
                }

                span {
                    text-align: center;
                }

                .asset-count {
                    padding-left: 7px;
                }
            }
        }

        // TODO: Consolidate from _widgets.scss
        .no-submissions {
            display: flex;
            align-items: center;
            justify-content: center;

            svg {
                color: $c-border;
            }

            .item-wrapper {
                display: flex;
                flex-direction: column;
                align-items: center;
                padding: 50px !important;

                p {
                    color: darken($c-border, 5);
                }
            }

            .item-actions {
                margin-top: 30px;
            }
        }

        .item-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        .item-asset {
            display: flex;
            flex-direction: row;
            border: 1px solid $c-border;
            border-radius: $border-radius;
            margin-bottom: 20px;
            width: 48%;

            transition: border .3s ease;

            &.selected {
                border-color: $c-blue;
            }

            .asset-thumbnail {
                border-right: 1px solid $c-border;
                max-width: 80px;

                img {
                    width: 100%;
                }
            }

            .asset-description {
                position: relative;
                display: flex;
                flex-direction: column;
                width: 100%;

                .download-asset {
                    position: absolute;
                    top: 10px;
                    right: 10px;
                }

                .asset-select {
                    position: absolute;
                    bottom: 10px;
                    right: 10px;
                    transition: all .3s ease;
                    color: $c-lightGray;
                    cursor: pointer;

                    &:hover {
                        color: darken($c-lightGray, 20)
                    }

                    &.active {
                        color: $c-blue;
                    }
                }

                .asset-title {
                    padding-right: 20px;
                }
            }

            .pad {
                padding: 10px;
            }
        }

        .item-title, .item-title a {
            color: $c-gray;
            font-size: 12px;
            font-family: $font-bold;
            text-decoration: none;
            transition: all .3s ease;

            &:hover {
                color: lighten($c-gray, 20);
            }
        }

        .item-meta {
            display: flex;
            margin-bottom: 3px;

            .item-meta-icon {
                margin-right: 5px;
                color: $c-lightGray;
            }

            span {
                font-size: 10px;
            }

            .item-meta-title {
                color: $c-lightGray;
                font-family: $font-regular;
            }

            .item-meta-right, .item-meta-right a {
                margin-left: auto;
                color: $c-lightGray;
            }

            .item-pipe {
                color: $c-lightGray;
                padding: 0 10px;
            }

        }
    }

    .star-rating {
        display: flex;

        .star {
            flex: 1;
            text-align: center;
            padding: 0 10px;

            svg {
                fill: rgba(black, .1);
            }

            &.active {
                svg {
                    fill: $c-yellow;
                }
            }
        }
    }


    .badge-verified {
        color: $c-blue;

        span {
            font-family: $font-regular;
            font-size: 10px;
            letter-spacing: .05em;
        }
    }

    #sidebar {
        background: transparent !important;
        box-shadow: none !important;
        padding: 0 !important;
        border-right: 1px solid $c-border;

        nav {
            margin: 0;
        }
        nav li a {
            padding: 5px 0;
            margin: 0;

            font-family: $font-roboto;

            &:hover {
                background: none;
            }

            &.sel {
                background: none;
                color: $c-darkPurple;
            }
        }
    }


    // Elements Data
    .elements-data {
        margin-top: 80px;
        padding: 0 15px;

        .ed-item {
            display: flex;
            font-family: $font-regular;
            font-size: 12px;
            color: $c-text;
            padding: 10px;
            @include antialias();

            span:first-child {
                padding-right: 6px;
            }
        }
    }

    .primary-meta {
        display: flex;
        align-items: center;
        margin: 10px 0 30px 0;
    }

    .rules-meta {
        margin-bottom: 10px;

        .txt {
            font-size: 10px;
            padding-right: 10px;
        }
        .flagged-rules {
            display: flex;
            align-items: center;

            svg {
                font-size: 16px;
                margin-right: 7px;
            }
        }

        .flagged-item {
            display: flex;
            align-items: center;
            text-transform: uppercase;
            font-family: $font-bold;
            font-size: 10px;
            letter-spacing: .05em;
            line-height: 1;
            padding: 4px 8px;
            border: 1px solid $c-border;
            border-radius: 5px;
            margin-right: 5px;
            margin-bottom: 5px;
        }
    }

    .status-badge {
        display: inline-block;
        border-radius: 20px;
        padding: 5px 15px;
        margin-right: 15px;
        text-align: center;
        text-transform: uppercase;
        font-size: 10px;
        font-weight: 600;
        letter-spacing: .07em;
        color: white;

        &.status-pending { background: $c-blue; }
        &.status-approved { background: $c-green; }
        &.status-rejected { background: $c-red; }
    }

    .created-date {
        font-size: 10px;
        font-weight: 600;
        color: $c-gray;
    }

    #main-container #main #main-content #details {
        padding: 0;
    }

    // Metas
    .meta {
        overflow: hidden;
        position: relative;
        display: flex;
        flex-direction: column;
        margin: 0;

        h2 {
            font-size: 14px;
            margin: 0;
            @include antialias;
        }

        &.centered {
            align-items: center;

            .meta-item {
                text-align: center;
            }
        }

        &.muted {
            align-self: flex-start;
            background: rgba($c-border, .3);
        }

        &.purple-bg {
            background: $c-purple;

            .meta-label {
                color: lighten($c-purple, 15);
            }

            h2 {
                color: white;
            }
        }

        &.white-bg {
            background-color: white;

            .meta-label {
                color: $c-lightGray;
            }
        }

        .meta-item {
            margin-bottom: 20px;

            &:last-child, &.single {
                margin-bottom: 0;
            }
        }
    }

    .star-rating-meta {
        padding: 40px 24px !important;

        h2 {
            text-align: center;
            margin-bottom: 12px;
        }
    }

    // Product Meta
    .meta.product-meta {
        .meta-label {
            font-size: 10px;
            font-family: $font-bold;
            text-transform: uppercase;
        }

        .meta-actions {
            margin-top: 20px;
        }
    }

    // Guest Meta
    .meta.guest-meta {
        padding: 35px 24px !important;

        .guest-badge {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            line-height: 1;
            padding: 20px;
            margin-bottom: 20px;
            background: $c-blue;
            font-family: $font-bold;
            font-size: 22px;
            width: 30px;
            height: 30px;
            color: white;
        }

        .meta-label {
            font-family: $font-semiBold;
            font-size: 10px;
            letter-spacing: .1em;
            text-transform: uppercase;
            @include antialias;
        }

        .badge-verified {
            color: $c-blue;
            margin-top: 5px;

            span {
                font-family: $font-regular;
                font-size: 10px;
                letter-spacing: .05em;
            }
        }
    }


    // Meta Actions
    .meta-actions {

        .mail-result {
            font-family: $font-semiBold;
            font-size: 14px;
            color: $c-lightGray;
            @include antialias;
        }
    }

    .btn-link {
        display: inline-block;
        font-size: 12px;
        margin-top: 5px;
        transition: all .3s ease;

    }

    .icon-right {
        position: relative;

        .meta-item {
            padding-right: 50px;
        }

        .icon {
            position: absolute;
            top: 50%;
            right: 15px;
            color: white;
            font-size: 40px;
            margin-top: -20px;
        }
    }

    .submission-details {
        font-family: $font-regular;
        font-size: 10px;
        color: $c-lightGray;
        text-transform: uppercase;
        letter-spacing: .03em;
        background: $c-border !important;
        padding: 24px;
        border-radius: 0;

        @include antialias();

        span {
            font-family: $font-semiBold;
            letter-spacing: normal;
            text-transform: none;
            color: darken($c-lightGray, 15);
        }
    }
}

#section-coming-soon {
    height: 100%;

    .coming-soon-wrapper {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    svg {
        width: 100px;
        opacity: .2;
    }

    h2 {
        color: #d8e3ec;
    }
}

