.flex-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.flex-container > div {
  padding-right: 25px;
  box-sizing: border-box;
}

.flex-container .full-col {
  width: 100%;
}

.flex-container .third-col {
  width: 65%;
}

.flex-container .half-col {
  width: 50%;
}

.flex-container .quarter-col {
  width: 35%;
}

@media only screen and (max-width: 998px) {
  .flex-container .quarter-col, .flex-container .third-col {
    width: 50%;
  }
}

@media only screen and (max-width: 767px) {
  .flex-container > div {
    padding: 0;
  }
  .flex-container .quarter-col, .flex-container .half-col, .flex-container .third-col {
    width: 100%;
  }
}

.flex-container .entry-settings {
  background: #E9EFF4;
  padding: 24px;
  border-radius: 4px;
}

.flex-container .col-header {
  margin-bottom: 20px;
}

.flex-container .col-header h2 {
  color: #8b8c8f;
}

@media only screen and (max-width: 1223px) {
  .qarr #crumbs {
    position: relative;
    padding: 0 !important;
  }
  .qarr #crumbs #nav-toggle {
    position: absolute;
    z-index: 100;
    left: 9px;
  }
}

.qarr.qarr-edit #main-container #main #main-content #content-container #content {
  background: white;
}

.qarr #main-container #main #main-content #content-container #content {
  background: #E9EFF4;
}

.qarr #main-container #main #header {
  height: 98px;
  background: white;
  padding: 24px !important;
  box-shadow: 1px 2px 10px -2px rgba(0, 0, 0, 0.1);
  justify-content: flex-start;
}

.qarr .page-title-wrapper {
  display: flex;
  align-items: center;
}

.qarr .page-title-wrapper .page-title h1 {
