}

.qarr .page-title-wrapper .page-title p {
  margin: 0;
  line-height: 1;
  font-family: "OpenSans-Italic", "Helvetica", sans-serif;
  color: #a5a6a8;
}

.qarr .entry-title-wrapper {
  display: flex;
  align-items: center;
  padding: 10px 0 30px 0;
}

.qarr .entry-title-wrapper .entry-title {
  display: flex;
  align-items: center;
}

.qarr .entry-title-wrapper .title-icon {
  margin-right: 8px;
  font-size: 20px;
}

.qarr .entry-title-wrapper h1 {
  margin: 0 10px 0 0;
