}

.qarr .qarr-logo {
  display: block;
  height: 29px;
  width: 45px;
  margin-right: 20px;
}

.qarr .list .list-item {
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  transition: all .3s ease;
  border-top: 1px solid #E9EFF4;
  min-height: 43px;
}

.qarr .list .list-item:last-child .item-wrapper {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.qarr .list .list-item.dragging .item-wrapper {
  border-right: 1px solid #E9EFF4;
  border-left: 1px solid #E9EFF4;
}

.qarr .list .list-item.approve-item .action-other {
  display: none;
}

.qarr .list .list-item.reject-item .action-approve {
  opacity: 0;
}

.qarr .list .list-item.delete-item .action-approve, .qarr .list .list-item.delete-item .action-other {
  opacity: 0;
}

.qarr .list .list-item .item-actions {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: rgba(233, 239, 244, 0.4);
}

.qarr .list .list-item .item-actions .action-other {
  margin-left: auto;
}

.qarr .list .list-item .item-actions > div {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity .3s ease;
}

.qarr .list .list-item .item-actions svg {
  transition: all .3s ease;
}

.qarr .list .list-item .item-actions a {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 26px;
  color: #a5a6a8;
  height: 100%;
  width: 100px;
}

.qarr .list .list-item .item-actions a.approved {
  color: #2fec94;
  animation: scaleGreen .3s ease-in-out both;
}

.qarr .list .list-item .item-actions a.rejected {
  color: #f07575;
  animation: scaleRed .3s ease-in-out both;
}

.qarr .list .list-item .item-actions .delete-btn:hover {
  color: #f07575;
}

.qarr .list .list-item .item-actions .reject-btn:hover {
  color: #f07575;
}

.qarr .list .list-item .item-wrapper {
  position: relative;
  z-index: 1;
  background-color: white;
  padding: 10px 25px;
}

.qarr .list .list-item.last {
  padding-bottom: 10px;
}

.qarr .list .list-item.has-hover:hover {
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.1);
  transition: all .3s ease-in-out;
  z-index: 1;
}

.qarr .list .list-item.element-actions {
  display: flex;
  padding: 0;
}

.qarr .list .list-item.element-actions button, .qarr .list .list-item.element-actions a {
  align-self: center;
  width: 100%;
  display: inline-block;
  border-radius: 0;
}

.qarr .list .list-item.element-actions span {
  text-align: center;
}

.qarr .list .list-item.element-actions .asset-count {
  padding-left: 7px;
}

.qarr .list .no-submissions {
  display: flex;
  align-items: center;
  justify-content: center;
}

.qarr .list .no-submissions svg {
  color: #E9EFF4;
}

.qarr .list .no-submissions .item-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px !important;
}

.qarr .list .no-submissions .item-wrapper p {
  color: #d8e3ec;
}

.qarr .list .no-submissions .item-actions {
  margin-top: 30px;
}

.qarr .list .item-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.qarr .list .item-asset {
  display: flex;
  flex-direction: row;
  border: 1px solid #E9EFF4;
  border-radius: 5px;
  margin-bottom: 20px;
  width: 48%;
  transition: border .3s ease;
}

.qarr .list .item-asset.selected {
  border-color: #4da1ff;
}

.qarr .list .item-asset .asset-thumbnail {
  border-right: 1px solid #E9EFF4;
  max-width: 80px;
}

.qarr .list .item-asset .asset-thumbnail img {
  width: 100%;
}

.qarr .list .item-asset .asset-description {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.qarr .list .item-asset .asset-description .download-asset {
  position: absolute;
  top: 10px;
  right: 10px;
}

.qarr .list .item-asset .asset-description .asset-select {
  position: absolute;
  bottom: 10px;
  right: 10px;
  transition: all .3s ease;
  color: #a5a6a8;
  cursor: pointer;
}

.qarr .list .item-asset .asset-description .asset-select:hover {
  color: #727375;
}

.qarr .list .item-asset .asset-description .asset-select.active {
  color: #4da1ff;
}

.qarr .list .item-asset .asset-description .asset-title {
  padding-right: 20px;
}

.qarr .list .item-asset .pad {
  padding: 10px;
}

.qarr .list .item-title, .qarr .list .item-title a {
  color: #474747;
  font-size: 12px;
  font-family: "OpenSans-Bold", "Helvetica", sans-serif;
  text-decoration: none;
  transition: all .3s ease;
}

.qarr .list .item-title:hover, .qarr .list .item-title a:hover {
  color: #7a7a7a;
}

.qarr .list .item-meta {
  display: flex;
  margin-bottom: 3px;
}

.qarr .list .item-meta .item-meta-icon {
  margin-right: 5px;
  color: #a5a6a8;
}

.qarr .list .item-meta span {
  font-size: 10px;
}

.qarr .list .item-meta .item-meta-title {
  color: #a5a6a8;
  font-family: "OpenSans-Regular", "Helvetica", sans-serif;
}

.qarr .list .item-meta .item-meta-right, .qarr .list .item-meta .item-meta-right a {
  margin-left: auto;
  color: #a5a6a8;
}

.qarr .list .item-meta .item-pipe {
  color: #a5a6a8;
  padding: 0 10px;
}

.qarr .star-rating {
  display: flex;
}

.qarr .star-rating .star {
  flex: 1;
  text-align: center;
  padding: 0 10px;
}

.qarr .star-rating .star svg {
  fill: rgba(0, 0, 0, 0.1);
}

.qarr .star-rating .star.active svg {
  fill: #FFD012;
}

.qarr .badge-verified {
  color: #4da1ff;
}

.qarr .badge-verified span {
  font-family: "OpenSans-Regular", "Helvetica", sans-serif;
  font-size: 10px;
  letter-spacing: .05em;
}

.qarr #sidebar {
  background: transparent !important;
  box-shadow: none !important;
  padding: 0 !important;
  border-right: 1px solid #E9EFF4;
}

.qarr #sidebar nav {
  margin: 0;
}

.qarr #sidebar nav li a {
  padding: 5px 0;
  margin: 0;
  font-family: "OpenSans-Regular", "Helvetica", sans-serif;
}

.qarr #sidebar nav li a:hover {
  background: none;
}

.qarr #sidebar nav li a.sel {
