  color: #966df3;
}

.qarr .elements-data {
  margin-top: 80px;
  padding: 0 15px;
}

.qarr .elements-data .ed-item {
  display: flex;
  font-family: "OpenSans-Regular", "Helvetica", sans-serif;
  font-size: 12px;
  color: #595D66;
  padding: 10px;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.qarr .elements-data .ed-item span:first-child {
  padding-right: 6px;
}

.qarr .primary-meta {
  display: flex;
  align-items: center;
  margin: 10px 0 30px 0;
}

.qarr .rules-meta {
  margin-bottom: 10px;
}

.qarr .rules-meta .txt {
  font-size: 10px;
  padding-right: 10px;
}

.qarr .rules-meta .flagged-rules {
  display: flex;
  align-items: center;
}

.qarr .rules-meta .flagged-rules svg {
  font-size: 16px;
  margin-right: 7px;
}

.qarr .rules-meta .flagged-item {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-family: "OpenSans-Bold", "Helvetica", sans-serif;
  font-size: 10px;
  letter-spacing: .05em;
  line-height: 1;
  padding: 4px 8px;
  border: 1px solid #E9EFF4;
  border-radius: 5px;
  margin-right: 5px;
  margin-bottom: 5px;
}

.qarr .status-badge {
  display: inline-block;
  border-radius: 20px;
  padding: 5px 15px;
  margin-right: 15px;
  text-align: center;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 600;
  letter-spacing: .07em;
  color: white;
}

.qarr .status-badge.status-pending {
