}

.qarr .status-badge.status-rejected {
  background: #f07575;
}

.qarr .created-date {
  font-size: 10px;
  font-weight: 600;
  color: #474747;
}

.qarr #main-container #main #main-content #details {
  padding: 0;
}

.qarr .meta {
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
}

.qarr .meta h2 {
