}

.qarr .meta.guest-meta .guest-badge {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  line-height: 1;
  padding: 20px;
  margin-bottom: 20px;
  background: #4da1ff;
  font-family: "OpenSans-Bold", "Helvetica", sans-serif;
  font-size: 22px;
  width: 30px;
  height: 30px;
  color: white;
}

.qarr .meta.guest-meta .meta-label {
  font-family: "OpenSans-SemiBold", "Helvetica", sans-serif;
  font-size: 10px;
  letter-spacing: .1em;
  text-transform: uppercase;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.qarr .meta.guest-meta .badge-verified {
  color: #4da1ff;
  margin-top: 5px;
}

.qarr .meta.guest-meta .badge-verified span {
  font-family: "OpenSans-Regular", "Helvetica", sans-serif;
  font-size: 10px;
  letter-spacing: .05em;
}

.qarr .meta-actions .mail-result {
  font-family: "OpenSans-SemiBold", "Helvetica", sans-serif;
  font-size: 14px;
  color: #a5a6a8;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.qarr .btn-link {
  display: inline-block;
  font-size: 12px;
  margin-top: 5px;
  transition: all .3s ease;
}

.qarr .icon-right {
  position: relative;
}

.qarr .icon-right .meta-item {
  padding-right: 50px;
}

.qarr .icon-right .icon {
  position: absolute;
  top: 50%;
  right: 15px;
  color: white;
  font-size: 40px;
  margin-top: -20px;
}

.qarr .submission-details {
  font-family: "OpenSans-Regular", "Helvetica", sans-serif;
  font-size: 10px;
  color: #a5a6a8;
  text-transform: uppercase;
