.qarr .panel .panel-wrapper {
  position: relative;
  background: white;
  border-radius: 5px;
  padding: 30px;
  box-sizing: border-box;
  border: 1px solid #E9EFF4;
}

.qarr .panel .panel-wrapper .status-badge {
  position: absolute;
  top: 20px;
  right: 20px;
}

.qarr .panel.panel-correspondence, .qarr .panel.panel-fields {
  margin-top: 50px;
}

.qarr .panel.panel-blue .panel-header {
  background: #4da1ff;
}

.qarr .panel .panel-header {
  position: relative;
  display: flex;
  margin: -30px -30px 0 -30px;
  padding: 25px;
  color: white;
  background: #c7c6c6;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.qarr .panel .panel-header h2 {
  font-family: "OpenSans-Bold", "Helvetica", sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: .03em;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.qarr .panel .panel-header .header-icon {
  position: absolute;
  right: 25px;
  margin-top: -3px;
  font-size: 30px;
}

.qarr .panel .panel-body {
  padding-top: 25px;
}

.qarr .panel .panel-body p {
  margin: 0;
}

.qarr .panel .panel-body hr {
  margin: 20px -30px;
}

.qarr .panel .panel-item {
  margin-bottom: 15px;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.qarr .panel .panel-item:last-child {
  margin-bottom: 0;
}

.qarr .panel .panel-item .panel-meta {
  font-size: 10px;
  color: #a5a6a8;
}

.qarr .panel .large-text {
  font-family: "OpenSans-Regular", "Helvetica", sans-serif;
  font-size: 16px;
  margin: 0;
}

.qarr .panel .large-text span {
  line-height: 1.6;
}

.qarr .panel.has-response .panel-response {
  margin-top: 50px;
  padding: 35px 25px 20px 25px;
  border: 1px dashed #d8e3ec;
  border-radius: 5px;
  background: #d8e3ec1f;
}

.qarr .panel .panel-actions {
  position: relative;
  display: flex;
  margin-top: 25px;
}

.qarr .panel .pull-right {
  margin-left: auto;
}

.qarr .panel .remove-entry {
  padding-left: 10px;
  margin-left: 10px;
  border-left: 1px solid #E9EFF4;
}

.qarr .panel .remove-entry .icon {
  position: static;
  widows: 100%;
}

.qarr .panel .remove-entry a {
  height: 40px;
  padding: 10px 3px;
}

.qarr .panel .panel-response {
  position: relative;
}

.qarr .panel .panel-response .spinner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.qarr .panel .panel-response .response-container.faded {
  opacity: .1;
}

.qarr .panel .panel-response .response-author {
  display: flex;
  position: absolute;
  top: 5px;
  left: 10px;
  width: 100%;
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  color: #a5a6a8;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.qarr .panel .panel-response .response-author span {
  color: #8b8c8f;
}

.qarr .panel .panel-response .response-author .last {
  margin-left: auto;
  margin-right: 20px;
}

.qarr .panel .panel-response .response-text {
  font-weight: 400;
  color: #727375;
}

.qarr .panel .panel-response .response-actions {
  display: flex;
  position: relative;
  margin-top: 15px;
}

.qarr .panel .panel-response .response-actions > a:last-child {
  margin-left: auto;
}

.qarr .panel .panel-response .response-actions svg {
  margin-right: 3px;
}

.qarr .panel .panel-response .response-actions a:hover {
  color: #4da1ff;
}

.qarr .panel .panel-response .response-actions #reply-to-feedback-btn {
  display: inline;
}

.qarr .panel .panel-progress {
  position: absolute;
  width: 300px;
  top: -22px;
  right: 0;
}

.qarr .panel .panel-progress .progressbar {
  top: 4px;
  left: auto;
  right: 0;
  width: 253px;
}

.qarr .panel .panel-progress .progressbar-status {
  font-family: "OpenSans-Regular", "Helvetica", sans-serif;
  font-weight: 600;
  letter-spacing: .05em;
}

.qarr .panel.panel-answers {
  margin-top: 30px;
}

.qarr .panel.panel-answers .large-text {
  padding: 5px 0;
}

.qarr .panel.panel-answers .panel-item {
  padding-bottom: 15px;
  border-bottom: 1px solid #E9EFF4;
}

.qarr .panel.panel-answers .panel-item:last-child {
  border: none;
  padding: 0;
}

.qarr .panel.panel-answers .panel-item-status {
  font-family: "OpenSans-Bold", "Helvetica", sans-serif;
  font-size: 10px;
  letter-spacing: .05em;
  text-transform: uppercase;
  min-height: 20px;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.qarr .panel.panel-answers .panel-item-status.status-pending {
  color: #4da1ff;
}

.qarr .panel.panel-answers .panel-item-status.status-approved {
  color: #2fec94;
}

.qarr .panel.panel-answers .panel-item-status.status-rejected {
  color: #f07575;
}

.qarr .panel.panel-answers .panel-actions {
  display: flex;
  margin-top: 5px;
}

.qarr .panel.panel-answers .panel-actions .qarr-pipe {
  color: #949393;
  font-size: 10px;
}

.qarr .panel.panel-answers .panel-actions a {
  display: block;
  font-family: "OpenSans-SemiBold", "Helvetica", sans-serif;
  font-size: 10px;
  color: #474747;
  padding: 0 4px;
  transition: all .3s ease;
}

.qarr .panel.panel-answers .panel-actions a:first-child {
  padding-left: 0;
}

.qarr .panel.panel-answers .panel-actions a:hover {
  color: #949393;
}

.qarr .panel.panel-answers .panel-actions a.btn-approve:hover {
  color: #2fec94;
}

.qarr .panel.panel-answers .panel-actions a.btn-reject:hover, .qarr .panel.panel-answers .panel-actions a.btn-delete:hover {
  color: #f07575;
}

.qarr .panel.panel-answers .panel-actions a.btn-delete {
  margin-left: auto;
}

.qarr .feedback-panel .matched-word {
  font-size: 14px;
  padding: 1px 4px;
  background: #E9EFF4;
  border-radius: 3px;
}

#header .utility-nav {
  margin: 0 30px 0 auto;
}

#header .utility-nav .utility-nav-item {
  opacity: .2;
}

#header .utility-nav .fa-layers {
  font-size: 18px;
}

.hud {
  position: absolute;
  display: none;
  top: 0;
}

.hud .hud-name {
  display: flex;
  font-family: "OpenSans-SemiBold", "Helvetica", sans-serif;
  font-size: 12px;
  margin-bottom: 20px;
}

.hud .hud-name .qarr-lightswitch {
  margin-left: auto;
}

.hud .qarr-field {
  margin: 0;
}

.hud .qarr-footer {
  display: flex;
  border-top: 1px solid #E9EFF4;
  transition: opacity .3s ease;
  padding: 0 !important;
}

.hud .qarr-footer > input {
  flex: 1;
  font-family: "OpenSans-Bold", "Helvetica", sans-serif;
  font-size: 10px;
  text-transform: uppercase;
  letter-spacing: .05em;
  line-height: normal;
  cursor: pointer;
  transition: all .3s ease;
  border-radius: 0;
}

.hud .qarr-footer > input:first-child {
  border-right: 1px solid #E9EFF4;
}

.hud .qarr-footer > input:hover {
  background: #E9EFF4;
}

.hud .qarr-footer .btn-modal.submit {
  margin-right: -5px;
}

.hud input[type="button"], .hud input[type="submit"] {
  display: block;
  outline: none;
  border: none;
  padding: 18px 0;
  margin: 0;
  height: 100%;
  background: white;
  cursor: pointer;
  -webkit-appearance: none;
