}

.qarr-field.search {
  border: none !important;
}

.qarr-field.custom-field {
  background: #fff;
  position: relative;
  border-radius: 6px;
  border: 1px solid #E9EFF4;
  transition: all .3s ease;
  min-height: 40px;
}

.qarr-field.custom-field:before {
  content: '';
  position: absolute;
  bottom: -1px;
  right: -1px;
  border-style: solid;
  border-width: 0 0 0 0;
  z-index: 4;
  border-color: #fff #f8f9fd;
  transition: all .3s ease .35s;
}

.qarr-field.custom-field.has-value input + label {
  transform: translate(0, -4px) scale(0.72);
}

.qarr-field.custom-field.has-value input + label + .qarr-input-icon {
  opacity: 1;
}

.qarr-field.custom-field.has-value textarea + label {
  transform: translate(0, -10px) scale(0.72);
}

.qarr-field.custom-field.has-value textarea + label + .qarr-input-icon {
  opacity: 1;
}

.qarr-field.custom-field.has-value .select2-container + label {
  transform: translate(0, -5px) scale(0.72);
}

.qarr-field.custom-field.has-value .select2-container + label + .qarr-input-icon {
  opacity: 1;
}

.qarr-field.custom-field.has-focus {
  border: 1px solid #B289EF;
}

.qarr-field.custom-field.has-error {
  animation: shake 0.6s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  border-color: #f07575;
}

.qarr-field.custom-field.has-error label {
  color: #f07575;
}

.qarr-field.custom-field.has-error:before {
  border-width: 38px 32px 0 0;
  border-color: #f07575 #f8f9fd;
  border-radius: 6px 0 0 0;
  transition-delay: 0s;
}

.qarr-field.custom-field.has-error .qarr-error-icon {
  color: #f07575;
  opacity: 1;
  transform: scale(1);
  transition: transform 0.4s cubic-bezier(0.87, -0.41, 0.19, 1.44) 0.35s, opacity 0.3s ease 0.45s;
