.btns, .qarr-modal .btns {
    position: relative;
    display: inline-flex;
    outline: none;
    text-transform: uppercase;
    text-decoration: none !important;
    text-align: center;
    box-sizing: border-box;
    font-family: $font-bold;
    font-size: 10px;
    font-weight: 600 !important;
    letter-spacing: .05em;
    color: $c-gray;
    padding: 10px 20px;
    //height: 40px;
    border: none;
    cursor: pointer;
    transition: all .3s ease;
    @include antialias;


    &.btn {
        height: auto !important;
        box-shadow: none;
    }

    &.btn-rounded {
        border-radius: 5px;
    }

    &.btn-mini {
        height: 100%;
        padding: 5px 10px;
    }

    &.btn-purple {
        color: white;
        background: $c-darkPurple;

        &:hover {
            background: lighten($c-darkPurple, 10);
        }
    }

    &.btn-disabled {
        opacity: .4;
        pointer-events: none;
        cursor: not-allowed;
    }

    &.btn-white {
        color: $c-gray !important;
        background: white;

        &:hover {
            color: white !important;
            background: $c-gray;
        }
    }

    &.btn-gray {
        background: $c-border;

        &:hover {
            color: white;
            background: $c-gray;
        }
    }

    &.btn-green {
        color: white;
        background: $c-green;

        &:hover {
            color: white;
            background: darken($c-green, 10);
        }
    }

    &.btn-red {
        color: white;
        background: $c-red;

        &:hover {
            color: white;
            background: darken($c-red, 10);
        }
    }

    &.btn-link {
        padding: 0;
        height: auto;
        text-transform: none;
    }

    &.btn-basic {
        padding: 0;
        height: auto;
        text-transform: none;
        letter-spacing: normal;
        font-family: $font-regular;
        font-size: 12px;
    }

    &.btn-in-purple {
        color: lighten($c-purple, 15);

        &:hover {
            color: white;
        }
    }

    &.customize-sources {
        display: block;
        width: 80%;
        margin: 30px auto;
        color: white !important;
    }

    &.btn-icon {
        .icon {
            display: flex;
            position: absolute;
            width: 35px;
            height: 100%;
            top: 0;
            left: 0;
            bottom: 0;
            align-items: center;
            justify-content: center;
        }

        .btn-text {
            padding-left: 15px;
        }
    }

    &.btn-icon-right {
        .icon {
            left: auto;
            right: 0;
        }

        .btn-text {
            padding-left: 0;
            padding-right: 15px;
        }
    }

    &.btn-modal {
        display: inline-block;
        outline: none;
        border: 0;
    }

    &.btn-on-red-bg {
        color: white;
        background: lighten($c-red, 5);

        &:hover {
            background: lighten($c-red, 2);
        }
    }
}

.qarr {

    .menu {
        margin-top: 10px;
        box-shadow: none;
        border: 3px solid white;
        border-radius: 5px;
        box-shadow: 0 0 5px 1px rgba($c-lightGray, .2);

        hr {
            margin: 10px -15px;
        }

        a {
            font-family: $font-bold;
            font-size: 12px;
            letter-spacing: .03em;
            @include antialias;

        }

        .sel {
            background: $c-border;

            &:before {
                content: '' !important;
            }
        }
    }

    #reply-email-btn {
        position: relative;

        .spinner {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }

        .link-text {
            &.hide {
                opacity: 0;
            }
        }
    }

    .menubtn {
        &:after {
            color: $c-lightGray;
        }
    }
}

.btn.submit:not(.disabled):not(.inactive):active, .btn.submit:not(.disabled):not(.inactive).active {
    background: darken($c-border, 5);
}


