.qarr {
    hr {
        border-color: $c-border;
        margin: 0;
    }
}

.qarr-waves {
    height: 50px;

    .wave {
        position: absolute;
        left:0;
        right:0;
        top:0;
        bottom:0;
        z-index: 1;
    }

    .wave + .wave {
        z-index: 2;
    }
}

.loader-container {
    position: absolute;
    z-index: 5;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0;
    border-radius: 5px;
}

.entry-status, .entry-badge, .product-badge, .status {
    color: white;
    width: 5px;
    height: 5px;
    margin: auto;
    border-width: 2px;
    border-style: solid;
    border-radius: 50px;
    background: transparent !important;
    transform-origin: center center;
    transition: all .3s ease;

    &.pending {
        border-color: $c-blue !important;
    }

    &.approved {
        border-color: $c-green !important;
    }

    &.rejected {
        border-color: $c-red !important;
    }

    &.purple {
        border-color: $c-purple !important;
    }
}

.entry-badge, .product-badge {
    &.pending {
        background: $c-blue !important;
    }

    &.purple {
        background: $c-purple !important;
    }
}

.purple-blanket {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: $c-purple;
    opacity: .8;
    transition: all .3s ease;
}



