.flex-container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;

    & > div {
        padding-right: 25px;
        box-sizing: border-box;
    }

    .full-col {
        width: 100%;
    }

    .third-col {
        width: 65%;
    }

    .half-col {
        width: 50%;
    }

    .quarter-col {
        width: 35%;
    }

    @include bp('max-width', $desktop) {
        .quarter-col, .third-col {
            width: 50%;
        }
    }

    @include bp('max-width', $tablet) {
        & > div {
            padding: 0;
        }

        .quarter-col, .half-col, .third-col {
            width: 100%;
        }
    }

    .entry-settings {
        background: $c-border;
        padding: 24px;
        border-radius: 4px;
    }

    .col-header {
        margin-bottom: 20px;

        h2 {
            color: darken($c-lightGray, 10);
        }
    }
}


