.qarr-modal {

    &.modal-blue {
        .header {
            background: $c-blue;
        }

        .lightswitch .label.on, .lightswitch:not(.dragging).on .label.off {
            background: $c-blue;
        }
    }

    .header {
        position: relative;
        display: flex;
        margin-bottom: 20px;
        padding-bottom: 20px;
        box-shadow: none;
        background: $c-purple;

        .icon {
            margin-left: auto;
            font-size: 22px;
        }

        .header-text {
            font-family: $font-roboto;
            font-size: 16px;
            font-weight: 600;
            text-transform: uppercase;
            letter-spacing: .03em;
            @include antialias;
        }

        span {
            display: block;
            color: white;
        }
    }

    textarea {
        min-width: 300px;
    }
}


.qarr {
    .prompt-modal {

        .btn {
            padding: 10px;
            height: auto;
            border-radius: 5px;
            background: $c-border;
            box-shadow: none !important;
            font-weight: 600;
            font-size: 10px;
            text-transform: uppercase;
            color: $c-gray;

            &.active {
                background: darken($c-border, 5);
            }
        }

        .prompt-msg, .options {
            font-family: $font-opensans;
            font-size: 12px;
            color: $c-gray;
        }

        .buttons {
            float: none;

            .btn {
                flex: 1;

                &:hover {
                    color: white;
                    background: $c-gray;
                }
            }
        }
    }
}


