.qarr {
    .text.focus, .text:focus, .border-box.focus, .border-box:focus, .selectize-text > .selectize-control > .selectize-input.focus, .selectize-text > .selectize-control > .selectize-input:focus {
        border-color: $c-border;
    }

    // Lightswitch
    .lightswitch .label.off, .lightswitch:not(.dragging):not(.on) .label.on {
        background: $c-border;
    }

    .lightswitch .label.on, .lightswitch:not(.dragging).on .label.off {
        background: $c-green;
    }

    .texticon.icon:before {
        top: 14px;
    }

    .qarr-field {
        margin-bottom: 10px;

        .heading {
            margin-bottom: 5px;
        }
    }
}

#title-format-items {
    display: flex;
    margin-bottom: 20px;

    .format-item {
        padding: 5px 10px;
        margin-right: 5px;

        &.format-example {
            font-size: 10px;
            letter-spacing: .03em;
            color: $c-lightGray;
        }

    }
}

.qarr-field-readonly {

    label {
        font-family: $font-semiBold;
        font-size: 12px;
        line-height: 1;
        letter-spacing: .05em;
        color: $c-textDark;
        @include antialias;
    }

    .field-value {
        font-family: $font-bold;
    }
}

.qarr-field {

    &.search {
        border: none !important;
    }

    &.custom-field {
        background: #fff;
        position: relative;
        border-radius: 6px;
        border: 1px solid $c-border;
        transition: all .3s ease;
        min-height: 40px;

        &:before {
            content: '';
            position: absolute;
            bottom: -1px;
            right: -1px;
            border-style: solid;
            border-width: 0 0 0 0;
            z-index: 4;
            border-color: #fff $c-grayBackground;
            transition: all .3s ease .35s;
        }

        &.has-value {
            input + label {
                transform: translate(0, -4px) scale(.72);

                & + .qarr-input-icon {
                    opacity: 1;
                }
            }

            textarea + label {
                transform: translate(0, -10px) scale(.72);

                & + .qarr-input-icon {
                    opacity: 1;
                }
            }

            .select2-container + label {
                transform: translate(0, -5px) scale(.72);

                & + .qarr-input-icon {
                    opacity: 1;
                }
            }
        }

        &.has-focus {
            border: 1px solid $c-purple;
        }

        &.has-error {
            animation: shake .6s cubic-bezier(.36, .07, .19, .97) both;
            border-color: $c-red;

            label {
                color: $c-red;
            }

            &:before {
                border-width: 38px 32px 0 0;
                border-color: $c-red $c-grayBackground;
                border-radius: 6px 0 0 0;
                transition-delay: 0s;
            }

            .qarr-error-icon {
                color: $c-red;
                opacity: 1;
                transform: scale(1);
                transition: transform .4s cubic-bezier(.87,-.41,.19,1.44) .35s, opacity .3s ease .45s;
            }
        }

        input {
            width: 100%;
            border: 0;
            outline: none;
            line-height: 20px;
            font-family: $font-opensans;
            font-weight: 500;
            font-size: 12px;
            letter-spacing: .05em;
            padding: 17px 40px 0 16px;
            background: none;
            color: $c-darkPurple;

            &:focus {
                & + label {
                    transform: translate(0, -4px) scale(.72);
                    & + .qarr-input-icon {
                        opacity: 1;
                    }
                }
            }
        }

        textarea {
            width: 100%;
            border: 0;
            outline: none;
            line-height: 20px;
            font-family: $font-opensans;
            font-weight: 500;
            font-size: 12px;
            letter-spacing: .05em;
            padding: 27px 40px 0 16px;
            background: none;
            color: $c-darkPurple;
            box-sizing: border-box;

            & + label {
                top: 30px;
            }
            &:focus {
                & + label {
                    transform: translate(0, -10px) scale(.72);
                    & + .qarr-input-icon {
                        opacity: 1;
                    }
                }
            }
        }

        label {
            position: absolute;
            z-index: 1;
            left: 16px;
            top: 50%;
            line-height: 24px;
            font-family: $font-regular;
            font-weight: 600;
            font-size: 12px;
            letter-spacing: .05em;
            color: $c-lightGray;
            margin: -12px 0 0 0;
            cursor: text;
            transform-origin: top left;
            transition: all .3s ease;
            @include antialias;
        }

        .qarr-input-icon {
            position: absolute;
            z-index: 2;
            stroke: none;
            fill: #3F4656;
            display: block;
            font-size: 14px;
            right: 16px;
            top: 50%;
            opacity: .2;
            cursor: pointer;
            transform: translate(0, -50%);
            transition: opacity .4s ease;
        }

        .qarr-error-icon {
            position: absolute;
            right: 0;
            bottom: 0;
            display: block;
            font-size: 14px;
            opacity: 0;
            stroke: none;
            z-index: 5;
            fill: #EB3E60;
            transform: scale(.4);
            transition: all .3s ease;
        }
    }

    &.field-white-bg {
        border-color: $c-border;

        &.has-error {
            &:before {
                border-width: 38px 32px 0 0;
                border-color: $c-red white;
                border-radius: 6px 0 0 0;
                transition-delay: 0s;
            }
        }
    }

    &.custom-select {
        .select2-container--default {
            width: 100% !important;
        }

        .select2-container--default .select2-selection--single {
            border: none;
            outline: none;
            border-radius: 10px;
        }

        .select2-container .select2-selection--single {
            min-height: 54px;
        }

        .select2-container .select2-selection--single .select2-selection__rendered {
            line-height: 20px;
            padding: 27px 40px 0 16px;
            color: $c-darkPurple;
            font-family: $font-regular;
            font-size: 12px;
            letter-spacing: .05em;
        }

        .select2-container--default .select2-selection--single .select2-selection__arrow {
            height: 100%;
            width: 54px;
        }

        .select2-container--default .select2-selection--multiple {
            border: none;
            outline: none;
            border-radius: 10px;
            min-height: 60px;
        }


        .select2-container--default .select2-selection--multiple .select2-selection__rendered {
            padding: 27px 40px 0 16px;

            li {

                &.select2-selection__choice {
                    border-color: $c-purple;
                    background-color: $c-purple;
                    font-family: $font-bold;
                    font-size: 10px;
                    color: white;
                }

                .select2-selection__choice__remove {
                    color: white;
                }
            }
        }
    }

}

.qarr {
    .select2-dropdown {
        border: 1px solid $c-purple;
        border-radius: 5px;
        top: 7px;
    }

    .select2-container {
        height: 100%;

        .select2-results__option {
            padding: 15px 0 15px 15px;
        }

        .select2-results__option--highlighted[aria-selected] {
            background-color: $c-purple;
        }

        .select2-results__option[aria-selected=true] {
            background-color: rgba($c-purple, .5);
            color: white;
        }
    }

    // Tags Input
    .tagify {

        tag {
            &:hover {
                div:before {
                    background: lighten($c-blue, 5) !important;
                }
            }

            x {
                font-family: $font-bold;
                font-size: 17px;
                color: white;

                &:hover {
                    color: black;
                    background: transparent;
                }
            }

            & > div {
                font-family: $font-bold;
                font-size: 10px;
                color: white;
                padding: 5px 25px 5px 10px;

                &:before {
                    background: $c-blue;
                }
            }
        }

        &.tagify--outside {
            border: none;
        }
    }
}

.tagify--outside{
    border: 0;
    padding: 0;
    margin: 0;
}
.tagify__input--outside{
    display: block;
    max-width: 600px;
    border: 1px solid #DDD;
    margin-top: 1.5em;
    margin-bottom: 1em;
}

@keyframes shake {
    10%, 90% {
        transform: translate3d(-1px, 0, 0);
    }
    20%, 80% {
        transform: translate3d(2px, 0, 0);
    }
    30%, 50%, 70% {
        transform: translate3d(-4px, 0, 0);
    }
    40%, 60% {
        transform: translate3d(4px, 0, 0);
    }
}


