.qarr {
    #sidebar-toggle {
         #selected-sidebar-item-label {
             font-family: $font-bold;
         }
    }

    #sidebar {
        ul {
            background: black;

            li {
                background: #ffffff;
            }
        }

        nav {
            background: white;
            padding: 0;

            li a {
                .badge {
                    margin: 0 0 0 auto;
                }
            }

            svg {
                color: darken($c-border, 5);
            }

            a {
                font-family: $font-bold !important;
                padding: 12px 24px !important;
                border-bottom: 1px solid $c-border;
                transition: all .3s ease;
                background: rgba($c-border, 0.3);
                color: darken($c-lightGray, 10);

                &.sel {
                    background: white !important;

                    svg {
                        color: $c-purple;
                    }
                }

                &:hover {
                    background: white !important;
                }
            }
        }

        .source-count {
            margin-left: auto;
            background: rgba($c-lightGray, .3);
            padding: 0 8px;
            border-radius: 5px;
            color: white;
            font-family: $font-bold;
            font-size: 10px;
            text-shadow: 0 0 3px rgba($c-lightGray, .5);
        }

        .badge {
            background: $c-purple !important;
            color: white !important;
        }
    }
}


